.giveaway-container {
  background: #f0fff7;
  border: 2px solid #a3d977;
  border-radius: 18px;
  box-shadow: 0 4px 16px rgba(163, 217, 119, 0.15);
  padding: 2rem 2.5rem;
  margin: 2rem auto;
  color: #29522e;
  font-family: 'Segoe UI', Arial, sans-serif;
  max-width: 1200px;
  text-align: center;
}

.giveaway-fruit-section {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}

.giveaway-fruit-img {
  width: 100px;
  height: 100px;
  border-radius: 16px;
  border: 2px solid #b6e388;
  background: #fff;
  object-fit: cover;      /* Ensures the image fills the box without distortion */
  box-shadow: 0 2px 8px rgba(0,0,0,0.07);
  display: block;
}

.giveaway-fruit-info h3 {
  margin: 0 0 0.3em 0;
  color: #388e3c;
  font-size: 1.3em;
}

.roblox-username {
  color: #ff2222;
  font-weight: bold;
  font-size: 1.1em;
}

.giveaway-socials {
  display: flex;
  justify-content: center;
  gap: 1em;
  margin-bottom: 1.5em;
}

.giveaway-social-btn {
  padding: 0.6em 1.2em;
  border-radius: 20px;
  font-weight: 600;
  text-decoration: none;
  color: white;
  font-size: 1em;
  transition: background 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  border: none;
  display: inline-block;
}

.giveaway-social-btn.youtube { background: #ff2222; }
.giveaway-social-btn.facebook { background: #1877f3; }
.giveaway-social-btn.tiktok { background: #000; }

.giveaway-social-btn:hover {
  filter: brightness(1.1);
  box-shadow: 0 4px 16px rgba(0,0,0,0.13);
}

.giveaway-form {
  margin-bottom: 1em;
}

.giveaway-form label {
  display: block;
  margin-bottom: 0.5em;
  font-weight: 500;
}

.giveaway-form input[type="text"] {
  width: 80%;
  padding: 0.6em 1em;
  border: 2px solid #b6e388;
  border-radius: 25px;
  font-size: 1em;
  margin-top: 0.5em;
  outline: none;
  transition: border-color 0.3s;
}

.giveaway-form input[type="text"]:focus {
  border-color: #7ed957;
}

.giveaway-form button {
  margin-top: 1em;
  padding: 0.6em 1.5em;
  background: #388e3c;
  color: white;
  border: none;
  border-radius: 20px;
  font-weight: bold;
  font-size: 1em;
  cursor: pointer;
  transition: background 0.2s;
}

.giveaway-form button:hover {
  background: #29522e;
}

.giveaway-success {
  background: #e0ffe6;
  color: #388e3c;
  border: 1.5px solid #b6e388;
  border-radius: 10px;
  padding: 1em;
  margin-bottom: 1em;
  font-weight: bold;
}

.giveaway-note {
  margin-top: 1em;
  color: #888;
  font-size: 0.95em;
}

.giveaway-main-flex {
  display: flex;
  gap: 2em;
  justify-content: center;
  align-items: flex-start;
  margin-top: 1.5em;
}

.giveaway-form-section {
  flex: 1 1 350px;
  min-width: 260px;
}

.giveaway-usernames-box {
  flex: 0 0 250px;
  background: #fff;
  border: 2px solid #b6e388;
  border-radius: 14px;
  box-shadow: 0 2px 8px rgba(163,217,119,0.07);
  padding: 1em;
  max-height: 260px;
  min-width: 180px;
  display: flex;
  flex-direction: column;
}

.giveaway-usernames-title {
  font-weight: bold;
  color: #388e3c;
  margin-bottom: 0.7em;
  font-size: 1.1em;
  text-align: center;
}

.giveaway-usernames-list {
  flex: 1;
  overflow-y: auto;
  max-height: 180px;
  padding-right: 4px;
}

.giveaway-username-item {
  padding: 0.4em 0.2em;
  border-bottom: 1px solid #e0ffe6;
  color: #29522e;
  font-size: 1em;
  text-align: left;
  word-break: break-all;
}

.giveaway-usernames-empty {
  color: #aaa;
  text-align: center;
  font-size: 0.97em;
  margin-top: 1.5em;
}

/* Responsive styles */
@media (max-width: 600px) {
  .giveaway-container {
    padding: 1rem 0.5rem;
    max-width: 98vw;
    margin: 1rem 0.5rem;
  }

  .giveaway-fruit-section {
    flex-direction: column;
    gap: 0.7rem;
    align-items: center;
    text-align: center;
  }

  .giveaway-fruit-img {
    width: 80px;
    height: 80px;
  }

  .giveaway-socials {
    flex-direction: column;
    gap: 0.7em;
  }

  .giveaway-form input[type="text"] {
    width: 100%;
    font-size: 1em;
  }
}

@media (max-width: 400px) {
  .giveaway-container {
    padding: 0.5rem 0.2rem;
  }
  .giveaway-fruit-img {
    width: 60px;
    height: 60px;
  }
}

